<template>
  <section class="faq">
    <Input
      :placeholder="t('faq.placeholder')"
      :opacity="true"
      class="faq__search"
    >
      <template #prepend>
        <Icon class="deposit-page__email-notify-bell" name="search" />
      </template>
    </Input>
    <Container
      v-for="item in faqData"
      :key="item.title"
      :muted="isFaqActive(item)"
      radius="0"
      class="faq__item"
      :class="{ 'faq__item__off-shadow': isFaqActive(item) }"
    >
      <div class="faq__item__group" @click="setActiveFaq(item)">
        <Text
          variant="h2"
          class="faq__item__title"
          :size="isMobile ? '14px' : '16px'"
          :color="isFaqActive(item) ? StyleVariables.primary : StyleVariables.black"
          weight="500"
          line-height="21px"
        >{{ item.title }}</Text>
        <Icon
          class="faq__item__expand"
          :name="isFaqActive(item) ? 'close' : 'plus'"
        />
      </div>
      <p v-show="isFaqActive(item)" class="faq__item__text">{{ item.text }}</p>
    </Container>

  </section>
</template>

<script>
import { ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import { useBreakpointsMedia } from '@/shared/plugins/breakpoints'
import Icon from '@/shared/components/Icon/Icon'
import Text from '@/shared/components/UIKit/Text/Text'
import Input from '@/shared/components/UIKit/Input'
import Container from '@/shared/components/Container'
import StyleVariables from '@/shared/styles/variables/StyleVariables'

export default {
  name: 'Faq',
  components: { Text, Container, Input, Icon },
  setup () {
    const { t } = useI18n()
    const { isMobile } = useBreakpointsMedia()

    const faqData = ref([
      {
        title: 'About forwarding limits',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      },
      {
        title: 'Using two-step verification',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      },
      {
        title: 'Usage of your phone number and address book',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      },
      {
        title: 'Use strong password',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      },
      {
        title: 'End-to-end encryption for business messages',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      },
      {
        title: 'How to link and use the security key',
        text: `When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.
          Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.`,
        isShow: false
      }
    ])
    const activeFaq = ref(null)

    const setActiveFaq = item => {
      if (activeFaq.value === item.title) {
        activeFaq.value = null
        return
      }
      activeFaq.value = item.title
    }
    const isFaqActive = item => {
      return activeFaq.value === item.title
    }

    return {
      t,
      faqData,
      isMobile,
      activeFaq,
      StyleVariables,
      isFaqActive,
      setActiveFaq
    }
  }
}
</script>

<style lang="scss">
.faq {

  &__search {
    margin: 0 20px;
  }
  &__item {
    position: relative;
    padding: 18px 44px 18px 20px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    &:first-of-type {
      margin-top: 20px;
    }
    &__off-shadow {
      box-shadow: none !important;
      border-color: transparent;
    }
    &__group {
      display: flex;
      justify-content: space-between;
      cursor: pointer;
    }

    &__text {
      margin-top: 12px;
      margin-bottom: 12px;
      font-size: 16px;
      line-height: 24px;
    }

    &__expand {
      position: absolute;
      top: 18px;
      right: 25px;
      cursor: pointer;
    }
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    &__item {
      padding: 18px 44px 20px 20px;
      &__off-shadow {
        .icon {
          opacity: 1 !important;

          path {
            stroke: map-get($colors, "primary");
          }
        }
      }
      &__text {
        font-size: 14px;
      }
      &__expand {
        position: absolute;
        top: 21px;
        right: 10px;
        cursor: pointer;
      }
      &:nth-child(even) &__expand {
        opacity: 0.5;
      }
    }
  }
}
